@import url(https://fonts.googleapis.com/css?family=Hind&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-white: #fff;
  --color-black: #000;

  --color-ui: #e7e7e7;
  --color-ability: #a42e1f;
  --color-card-border: #fee264;

  /* types */
  --color-grass: #93ca5b;
  --color-fire: #e64f4a;
  --color-water: #4bb5ea;
  --color-lightning: #fcd333;
  --color-fighting: #e4552d;
  --color-psychic: #cd8cbb;
  --color-colorless: #f0edeb;
  --color-darkness: #4d636c;
  --color-metal: #9c9991;
  --color-dragon: #a9a95f;
  --color-fairy: #e66b9c;
}

.card {
  display: flex;
  width: 368px;
  height: 512px;
  background-color: #fee264;
  background-color: var(--color-card-border);
  border-radius: 16px;
  font-family: Hind, sans-serif;
}

.card * {
  box-sizing: border-box;
}

.card__container {
  flex-grow: 1;
  margin: 14px;
  border-radius: 6px;
  border: 1px solid #000;
  border: 1px solid var(--color-black);
  background-color: #e7e7e7;
  background-color: var(--color-ui);
}

.grass .card__container {
  background-color: #93ca5b;
  background-color: var(--color-grass);
}
.fire .card__container {
  background-color: #e64f4a;
  background-color: var(--color-fire);
}
.water .card__container {
  background-color: #4bb5ea;
  background-color: var(--color-water);
}
.lightning .card__container {
  background-color: #fcd333;
  background-color: var(--color-lightning);
}
.fighting .card__container {
  background-color: #e4552d;
  background-color: var(--color-fighting);
}
.psychic .card__container {
  background-color: #cd8cbb;
  background-color: var(--color-psychic);
}
.colorless .card__container {
  background-color: #f0edeb;
  background-color: var(--color-colorless);
}
.darkness .card__container {
  background-color: #4d636c;
  background-color: var(--color-darkness);
}
.metal .card__container {
  background-color: #9c9991;
  background-color: var(--color-metal);
}
.dragon .card__container {
  background-color: #a9a95f;
  background-color: var(--color-dragon);
}
.fairy .card__container {
  background-color: #e66b9c;
  background-color: var(--color-fairy);
}

.card__header {
  display: flex;
  height: 32px;
  font-weight: bold;
}

.card__subtype {
  flex-grow: 0;
  min-width: 50px;
}

.card__subtype--pill {
  display: inline-block;
  margin-top: 5px;
  margin-left: -5px;
  padding: 0 4px;
  min-width: 52px;
  height: 16px;
  line-height: 16px;
  background-color: #e7e7e7;
  background-color: var(--color-ui);
  border: 1px solid #000;
  border: 1px solid var(--color-black);
  border-radius: 12px 4px;
  font-size: 10px;
  font-style: italic;
  text-transform: uppercase;
  text-align: center;
}

.card__name {
  flex-grow: 1;
  padding: 7px 5px;
  line-height: 22px;
  font-size: 22px;
}

.card__hp {
  flex-grow: 0;
  width: 50px;
  text-align: right;
}

.card__hp--text {
  font-size: 10px;
}

.card__hp--value {
  font-size: 25px;
}

.card__types {
  flex-grow: 0;
  width: 32px;
}

.card__type {
  margin: 2px;
  width: 28px;
  height: 28px;
  background-color: black;
  border-radius: 50%;
  border: 2px solid #fff;
  border: 2px solid var(--color-white);
}

.card__type--colorless {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--darkness {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--dragon {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--fairy {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--fighting {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--fire {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--grass {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--lightning {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--metal {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--psychic {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}
.card__type--water {
  background-image: url(data:image/png;base64,);
  background-size: cover;
}

.card__general--image {
  background-color: #fff;
  background-color: var(--color-white);
  /* background-image: url(https://vignette.wikia.nocookie.net/pokemon/images/7/70/Ash_Heracross.png/revision/latest?cb=20150917062521); */
  background-size: cover;
  margin: 0 10px;
  height: 198px;
  border: 1px solid #000;
  border: 1px solid var(--color-black);
}

.card__general--info {
  background-color: #e7e7e7;
  background-color: var(--color-ui);
  margin: -2px 5px;
  padding: 2px;
  font-size: 8px;
  text-align: center;
  border: 1px solid #000;
  border: 1px solid var(--color-black);
  border-radius: 12px 4px;
}

.card__body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 10px;
  height: 182px;
}

.card__body--with-ability {
  justify-content: flex-start;
}

.card__ability {
  margin-top: 15px;
  margin-bottom: 12px;
}

.card__ability--header {
  display: flex;
  flex-direction: row;
}

.card__ability--type {
  margin-top: 3px;
  margin-left: -5px;
  margin-right: 5px;
  padding: 0 4px;
  width: 94px;
  line-height: 16px;
  height: 16px;
  background-color: #a42e1f;
  background-color: var(--color-ability);
  color: #fff;
  color: var(--color-white);
  border-radius: 12px 4px;
  font-size: 10px;
  font-weight: bold;
  font-style: italic;
  text-align: center;
}

.card__ability--name {
  color: #a42e1f;
  color: var(--color-ability);
  font-weight: bold;
}

.card__ability--text {
  font-size: 12px;
  line-height: 14px;
  margin: 0 5px;
}

.card__attack {
  margin-bottom: 24px;
}

.card__attack:last-of-type {
  margin-bottom: 0;
}

.card__attack--header {
  display: flex;
  height: 24px;
  flex-direction: row;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.card__attack--cost {
  width: 94px;
  height: 24px;
}

.card__attack--cost-type {
  background-color: black;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: -1px 1px 0;
  border-radius: 50%;
  border: 1px solid #fff;
  border: 1px solid var(--color-white);
}

.card__attack--name {
  flex-grow: 1;
  height: 24px;
  line-height: 24px;
}

.card__attack--damage {
  font-size: 20px;
  height: 24px;
  line-height: 24px;
}

.card__attack--text {
  font-size: 12px;
  line-height: 14px;
  margin: 0 5px;
}

.card__footer {
  display: flex;
  flex-direction: row;
  margin: 0 -5px;
  padding: 1px 10px;
  height: 16px;
  background-color: #e7e7e7;
  background-color: var(--color-ui);
  border: 1px solid #000;
  border: 1px solid var(--color-black);
  border-radius: 12px 4px;
  font-size: 8px;
  font-weight: bold;
  line-height: 12px;
}

.card__footer--item {
  display: flex;
  flex-grow: 1;
}

.card__footer--value {
  margin-left: 4px;
}

.card__footer--item .card__type-small {
  margin-left: 12px;
}

.card__type-small {
  background-color: black;
  margin-left: 2px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.card__description {
  padding-top: 4px;
  padding-left: 148px;
  padding-right: 4px;
  max-height: 186px;
  font-size: 9px;
  line-height: 12px;
  text-align: right;
}

.form {
  width: 500px;
}

@media print {
  .form {
    display: none;
  }
}
.form * {
  box-sizing: border-box;
}

.form textarea {
  resize: none;
}

.form,
.form input,
.form textarea,
.form select {
  font-family: Hind, sans-serif;
  font-size: 14px;
  line-height: 21px;
}

.form input,
.form textarea,
.form select {
  width: 100%;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.form select {
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23ccc%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: right 0.7em top 50%;
  background-size: 0.65em auto;
}

.group {
  display: flex;
  flex-flow: row wrap;
}

.field {
  flex-grow: 1;
  padding: 5px;
}

.field label {
  padding: 6px 6px 0;
}

.field input,
.field textarea,
.field select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.field label {
  display: block;
}

.field__name {
  width: 50%;
}
.field__hp {
  width: 25%;
}
.field__types {
  width: 25%;
}

.field__category {
  width: 50%;
}
.field__height {
  width: 25%;
}
.field__weight {
  width: 25%;
}

.field__ability-name {
  width: 33.333%;
}
.field__ability-text {
  width: 66.666%;
}

.field__attacks-0-name {
  width: 33.333%;
}
.field__attacks-0-text {
  width: 66.666%;
}

.field__attacks-0-cost {
  display: flex;
  flex-flow: row wrap;
  width: 50%;
}
#cost-all,
#cost-colorless {
  width: 55px;
}
.field__attacks-0-damage {
  width: 25%;
  margin-right: 25%;
}
label[for="cost-all"] {
  width: 100%;
}

.app {
  display: flex;
  min-width: 908px;
  min-height: max(100vh, 600px);
  justify-content: center;
  align-items: center;
}

.app .form {
  margin-left: 40px;
}

